:root {
  --font-monospace: 'Source Code Pro', 'Fira Code', 'Consolas', 'Monaco',
    monospace;
  --font-body: 'Lato', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  --font-header: 'Nunito Sans', apple-system, BlinkMacSystemFont, 'Segoe UI',
    Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;

  --text: #2f4858;
  --text-med: #6d7e8a;
  --text-light: #c0c8cc;
  --text-lighter: #eaecee;
  --border-radius: 3px;
  --dark-red: #9a2e36;
  --red: #d33f49;
  --light-red: #e3848b;
  --orange: #ff6300;
  --gold: #c0af3f;
  --yellow: #f0db4f;
  --light-yellow: #f6e995;
  --purple: #993d70;
  --light-purple: #b7779a;
  --dark-pink: #b24e72;
  --hot-pink: #ff41b4;
  --pink: #ff70a3;
  --light-pink: #ffc5da;
  --dark-green: #338d70;
  --green: #44bc96;
  --light-green: #55ebbc;
  --navy: #001b44;
  --dark-blue: #466683;
  --blue: #4d8dc9;
  --light-blue: #75aadb;
  --lightest-blue: #bad4ed;
  --washed-blue: #f8fafd;
  --washed-green: #ecf8f4;
  --washed-yellow: #fefcf4;
  --washed-red: #fcf4f5;
  --washed-purple: #faf7f9;
}

html {
  font-family: var(--font-body);
  font-size: 18px;
}

body {
  display: grid;
  align-items: center;
  justify-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
}

.box-preview {
  display: flex;
}

.box-container+.box-container {
  margin-left: 2px;
}

.box-controls {
  margin: 1em auto;
  width: max-content;
}

.box {
  width: 100px;
  height: 100px;
  margin: 0em;
  padding: 0px;
  border: 0px solid;
  border-color: var(--dark-blue);
  background-color: var(--lightest-blue);
  transition: all 0.25s ease;
}

.box-inner {
  background-color: #ffffff88;
  width: 100%;
  height: 100%;
}

.box-container {
  background: var(--text-light);
  width: max-content;
  margin: 1em auto;
}

/* neat trick to ensure that margin is applied to top and bottom, too */
.box-container:before,
.box-container:after {
  content: ' ';
  display: table;
}

.box-container.is-content-box .box-inner {
  outline: 2px solid var(--purple);
  outline-offset: -1px;
}

.box-container.is-border-box .box {
  outline: 2px solid var(--purple);
  outline-offset: -1px;
}

main {
  height: 100%;
  display: grid;
  grid-template-rows: minmax(360px, 1fr) 1fr;
  align-items: center;
  justify-items: center;
}

.box-controls {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 4em;
}

@media only screen and (max-width: 700px) {
  .box-controls {
    transform: scale(0.8);
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, auto);
    grid-gap: 1em;
  }

  main {
    grid-template-rows: minmax(100px, 1fr) auto;
  }
}

@media only screen and (min-height: 800px) {
  main {
    grid-template-rows: minmax(650px, auto) 1fr;
  }
}

label {
  color: var(--text);
}

label+div {
  margin: 1em 0.5em;
}

#presets>label {
  display: block;
  grid-column: 1 / -1;
}

button {
  padding: 0.5em;
  font-size: 0.94em;
  background: white;
  color: var(--light-purple);
  border: 2px solid var(--light-purple);
}

#presets {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-template-rows: repeat(2, auto);
}

button+button {
  margin-left: 0;
  border-left: none;
}

button:first-of-type {
  border-radius: 5px 0 0 5px;
}

button:last-of-type {
  border-radius: 0 5px 5px 0;
}

button.active {
  background-color: var(--light-purple);
  color: white;
}

button:hover {
  background-color: var(--purple);
  border-color: var(--purple);
  color: white;
}

/* sliders */
.noUi-tooltip {
  display: none;
  font-family: var(--font-monospace);
}

.noUi-active .noUi-tooltip {
  display: block;
}

.noUi-target {
  background-color: var(--text-lighter);
  border-width: 0;
}

#slider-box-size .noUi-connect {
  background-color: var(--purple);
}

#slider-box-size .noUi-handle {
  background-color: var(--purple);
  border-color: var(--purple);
  box-shadow: 0 3px 6px -3px var(--text-med);
}

#slider-box-padding .noUi-connect {
  background-color: var(--lightest-blue);
}

#slider-box-padding .noUi-handle {
  background-color: var(--lightest-blue);
  border-color: var(--lightest-blue);
  box-shadow: 0 3px 6px -3px var(--text-med);
}

#slider-box-border .noUi-connect {
  background-color: var(--dark-blue);
}

#slider-box-border .noUi-handle {
  background-color: var(--dark-blue);
  border-color: var(--dark-blue);
  box-shadow: 0 3px 6px -3px var(--text-med);
}

#slider-box-margin .noUi-connect {
  background-color: var(--text-light);
}

#slider-box-margin .noUi-handle {
  background-color: var(--text-light);
  border-color: var(--text-light);
  box-shadow: 0 3px 6px -3px var(--text-med);
}